.DrumPad button {
  display: inline-block;
  width: 4.7em;
  height: 3.5em;
  text-align: center;
  text-decoration: none;
  font-weight: bold;
  border: none;
  text-align: center;
  background-color: rgb(82, 80, 80);
  border-radius: 5px;
  box-shadow: 3px 3px #888888;
  cursor: pointer;
}

.DrumPad button:hover {
  border: 1px solid rgb(1, 213, 228);
}
